@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

body {
  background-color: black;
}

.gd-container {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-width: 100%;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.gd-container-game {
  position: relative;
  width: 1000px;
  height: 500px;
  background-repeat: no-repeat;
  background-size: cover;
}

.gd-garden {
  position: absolute;
  left: 21.2%;
  top: 14%;
  width: 606px;
  height: 405px;
  display: grid;
  grid-template-columns: repeat(9, auto);
}

.gd-garden-item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: relative;
  background-color: #fff0;
  padding-bottom: 18px;
}

.gd-garden-image-opacity {
  position: absolute;
  bottom: 0;
  opacity: 0.4;
}

.gd-garden-image {
  width: 56px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gd-garden-image img {
  max-width: 100%;
}

.gd-sendBank {
  position: absolute;
  padding: 12px;
  left: 50px;
}

.gd-sendBank-item {
  position: relative;
  width: 90px;
  margin-bottom: 3px;
}

.gd-sendBank-item:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.gd-sendBank-item-price {
  width: 20px;
  position: absolute;
  right: 15px;
  bottom: 3px;
  font-size: 12px;
  font-weight: bold;
}

.gd-sendBank-item img {
  width: 100%;
}

.gd-sendBank-item img:nth-child(1) {
  display: block;
}

.gd-sendBank-item img:nth-child(2) {
  display: none;
}

.gd-sendBank-item.active img:nth-child(1) {
  display: none;
}

.gd-sendBank-item.active img:nth-child(2) {
  display: block;
}

.gd-coinBank {
  position: absolute;
  bottom: 20px;
  left: 50px;
  width: 128px;
  height: 31px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: cover;
}

.gd-coinBank-val {
  position: absolute;
  top: 60%;
  left: 65%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: yellow;
}

.gd-bank {
  position: absolute;
  top: 20px;
  right: 50px;
  width: 60px;
  height: 60px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: contain;
}

.gd-bank-image {
  width: 60px;
  height: 60px;
  background-size: contain;
}

.gd-bank-image:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.gd-water-drop-image {
  position: absolute;
  top: 5px;
  right: 5px;
}

.gd-coin-image {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 20px;
}

.gd-bank-price {
  position: absolute;
  bottom: 0;
  left: 5px;
  border-radius: 5px;
  font-weight: bold;
  color: yellow;
}

.gd-garden-item-timer {
  position: absolute;
  bottom: 5px;
  right: 5px;
  font-size: 10px;
}

.gd-garden-item-timer.over-timer {
  color: #b10909fc;
  font-weight: 700;
}

